.header {
  background-image: url("../../../images/bg-pattern.svg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1.25rem;
  height: 350px;
  text-align: center;
}
.header__descript {
  background-image: url("../../../images/pattern-circles.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding: 1.25rem;
}
.header__descript h1 {
  padding-bottom: 0.625rem;
  font-size: 1.25rem;
  font-weight: 800;
  color: var(--darkDesaturatedBlue-TextandCTA-Background);
}
.header__descript p {
  font-size: 0.9375rem;
  color: var(--grayishBlue-Text);
  max-width: 21ch;
  line-height: 1.6;
  margin-inline: auto;
}

.main {
  width: 80%;
  margin: auto;
  margin-block-start: -8.75rem;
  background-color: var(--white-PricingComponentBackground);
  border: 1px solid black;
  border-radius: 0.625rem;
  text-align: center;
}
.main__pageviews {
  padding: 1.25rem;
  color: var(--grayishBlue-Text);
  text-transform: uppercase;
  font-size: 0.9375rem;
  word-spacing: 0.3125rem;
  font-weight: 800;
}
.main__slider input[type=range] {
  -webkit-appearance: none;
  width: 95%;
  margin: 18px 0;
}
.main__slider input[type=range]:focus {
  outline: none;
}
.main__slider input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 0.625rem;
  background-color: var(--lightGrayishBlue-EmptySliderBar);
  border-radius: 1.25rem;
  cursor: pointer;
}
.main__slider input[type=range]::-webkit-slider-thumb {
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
  -webkit-appearance: none;
  background-image: url("../../../images/icon-slider.svg");
  background-color: var(--strongCyan-SliderBackround);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  margin-top: -0.8125rem;
}
.main__month {
  padding: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main__month h2 {
  font-size: 1.875rem;
  font-weight: 800;
  padding-right: 0.9375rem;
  color: var(--darkDesaturatedBlue-TextandCTA-Background);
}
.main__month p {
  color: var(--grayishBlue-Text);
  font-size: 0.9375rem;
}
.main fieldset {
  display: grid;
  grid-template-areas: "mon mon one two year minus" "mon mon one two year minus";
  border: none;
  font-size: 0.9375rem;
  color: var(--grayishBlue-Text);
  align-items: center;
}
.main fieldset legend:first-of-type {
  grid-area: mon;
  padding: 10px;
  font-size: 0.8125rem;
}
.main fieldset input[type=radio]:first-of-type {
  grid-area: one;
  justify-self: end;
}
.main fieldset input[type=radio]:last-of-type {
  grid-area: two;
  justify-self: end;
}
.main fieldset legend:last-of-type {
  grid-area: year;
  font-size: 0.8125rem;
}
.main fieldset p {
  grid-area: minus;
  font-size: 0.8125rem;
  background: var(--lightGrayishRed-DiscountBackground);
  color: var(--lightRed-DiscountText);
  border-radius: 1.25rem;
}
.main fieldset p span {
  display: none;
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}

:root {
  /* Primary */
  --softCyan-FullSliderBar: hsl(174, 77%, 80%);
  --strongCyan-SliderBackround: hsl(174, 86%, 45%);
  --lightGrayishRed-DiscountBackground: hsl(14, 92%, 95%);
  --lightRed-DiscountText: hsl(15, 100%, 70%);
  --paleBlue-CTA-Text: hsl(226, 100%, 87%);
  /* Neutral */
  --white-PricingComponentBackground: hsl(0, 0%, 100%);
  --veryPaleBlue-MainBackground: hsl(230, 100%, 99%);
  --lightGrayishBlue-EmptySliderBar: hsl(224, 65%, 95%);
  --lightGrayishBlue-ToggleBackground: hsl(223, 50%, 87%);
  --grayishBlue-Text: hsl(225, 20%, 60%);
  --darkDesaturatedBlue-TextandCTA-Background: hsl(227, 35%, 25%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background-color: var(--veryPaleBlue-MainBackground);
}